@import url(https://fonts.googleapis.com/css2?family=Cabin:wght@600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.square{
    font-family: 'Cabin';
    background: #fff;
    border: 1px solid #999;
    font-size: 100px;
    font-weight: 600;
    line-height: 34px;
    height: 150px;
    width: 150px;
    margin-right: -1px;
    margin-top: -1px;
    padding: 0;
    text-align: center;

}

.x{
    color: var(--cl1);
}
.o{
    color: var(--cl2);
}

.square:focus {
    outline: none;
}

.board{
  flex: 1.65 1;
}

.board-row {
  display: flex;
  flex-direction: row;
  /* background-color: crimson; */
  /* width: 60%; */
  align-items: center;
  justify-content: flex-end;
}

@media (max-width: 760px) {
  .board-row{
    justify-content: center;
  }
}

/* .game {
    display: flex;
    flex-direction: row;
  } */

:root{
  --cl1: #FB6542;
  --cl2: #375E97;
  --cl3: #3F681C;
  --cl4: #FFBB00;
}

body{
  border-top: 2px solid #FFBB00;
  border-top: 2px solid var(--cl4);
  border-right: 2px solid #FFBB00;
  border-right: 2px solid var(--cl4);
}

.game-title{
  text-align: center;
}

.game-status{
  text-align: center;
  font-size: 20px;
  font-weight: bold;
  margin-bottom: 20px;
  color: #999;
}

.game-board{
  display: flex;
}

@media (max-width: 760px) {
  .game-board{
    /* display: block; */
    flex-direction: column;
  }
}

.game-moves{
  flex: 1 1;
}
.game-moves ul{
  margin-top: 0;
}
@media (max-width: 760px) {
  .game-moves{
    display: flex;
    justify-content: center;
  }
  .game-moves ul{
    margin-top: 20px;
  }
}
.game-moves ul li{
   list-style: none;
}
.game-moves ul li button{
  padding: 10px;
  border: none;
  outline: none;
  border-radius: 5px;
  letter-spacing: 2px;
  margin-bottom: 10px;
  font-weight: bold;
  width: 200px;
  color: white;
  /* transition: 2s animation; */
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
  -webkit-animation-name: slidein;
          animation-name: slidein;
}

@-webkit-keyframes slidein {
  from {
    margin-bottom: 20%;
  }
  to {
    margin-left: 0%;
  }
}

@keyframes slidein {
  from {
    margin-bottom: 20%;
  }
  to {
    margin-left: 0%;
  }
}


.game-moves ul li:nth-child(even) button{
  background-color: #FB6542;
  background-color: var(--cl1);
}
.game-moves ul li:nth-child(odd) button{
  background-color: #375E97;
  background-color: var(--cl2);
}


